    color:white;
}
@keyframes shooting{
    from{
      opacity: 0;
      right:0px;
    }
    5%{
        opacity: 1;
        background-color: black;
        
    }
    to{
        opacity: 1;
        right:100%;
        background-color: black;
        transform: rotate( 1440deg );
    }
    
}
@keyframes shooting2{
    from{
        
        background-color: white;
    }
    to{
        
        background-color: white;
    }
}
.balls{
